@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');

*{
  box-sizing: border-box;
}

body{
  margin:0;
  font-family: 'Helvetica', sans-serif;
  background: linear-gradient(to right, #DA4453, #89216B);
  animation: gradient 20s ease infinite;
  background-size: 400% 400%;
  scroll-behavior: smooth;
}

.App{
  padding: 10px;
  color: white;
}

@keyframes gradient{
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.join-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
}

.join-section input{
  font-family: 'Helvetica', sans-serif;
  padding: 10px 0;
  margin-bottom: 10px;
  border-radius: 2px;
  border: none;
}


.join-section button{
  background: linear-gradient(to right, #FC5C7D, #6A82FB);
  border: none;
  color: white;
  padding: 10px;
  border-radius: 2px;
}



input:focus{
  outline: none;
}


@keyframes btngradient{
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}


button{
  font-family: 'PT Sans', sans-serif;
}


.chat-section{
  outline:1px solid rgb(36, 36, 36)
}

.chat-profile{
  font-family: 'PT Sans', sans-serif;
  padding: 10px;
  background: linear-gradient(to right, #DA4453, #89216B);
  font-weight:600;
  outline: 1px solid rgb(36, 36, 36);
}

.chat-display{
  height: 75vh;
  padding: 10px;
}

.message-container{
  width: 100%;
  height: 100%;
  /* overflow-y: scroll; */
  /* overflow-x: hidden; */
}

.message{
  /* outline: 1px solid white; */
  margin-top: 15px;
  
}


#you .message-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  /* width: 200px; */
  /* height: 40px; */
  /* outline: 1px solid white; */
  color: black;
  padding: 0 5px;
}

#other .message-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* width: 200px; */
  /* height: 40px; */
  /* outline: 1px solid white; */
  color: black;
  padding: 0 5px;
}
  



.message-content{
  width: 50%;
  border-radius: 7px;
  height: 40px;
  background: white;
  box-shadow: 0px 0px 10px 1px rgb(241, 241, 241) inset;
  /* outline: 1px solid red; */
  font-size: 15px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px;
}

#you .message-data{
  /* outline:1px solid red; */
  display:flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  width: 75px;
  height: 15px;
  margin-top: 3px;
  margin-right: 5px;
  color: rgb(182, 181, 181);
}

#other .message-data{
  /* outline:1px solid red; */
  display:flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  width: 75px;
  height: 15px;
  margin-top: 3px;
  margin-left: 5px;
  color: rgb(182, 181, 181);
}

.chat-send{
  /* outline: 1px solid red; */
  display: flex;
  outline: 1px solid rgb(36, 36, 36);
}

.chat-send input{
  width: 85%;
  padding: 10px 0;
}

.chat-send button{
  width: 15%;
  /* padding: 11px 0; */
  background: linear-gradient(to right, #FC5C7D, #6A82FB);
  border: none;
}


.send-icon{
  color: white;
  font-size: 16px;
}


input{
  text-indent: 5px;
  border: none;
}

button:hover{
  cursor: pointer;
  animation: btngradient 5s ease infinite;
  background-size: 150% 150%;
  transform: scale(1.1);
}

button:active{
  transform: scale(1)
}


@media(min-width: 600px){
  .App{
    padding: 10px 50px;
  }
}